@font-face {
	font-family: 'footwork';
	src:url('/fonts/footwork.eot?-um2iai');
	src:url('/fonts/footwork.eot?#iefix-um2iai') format('embedded-opentype'),
		url('/fonts/footwork.woff?-um2iai') format('woff'),
		url('/fonts/footwork.ttf?-um2iai') format('truetype'),
		url('/fonts/footwork.svg?-um2iai#footwork') format('svg');
	font-weight: normal;
	font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
	font-family: 'footwork';
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

